 body{
    align-items: center;
    justify-content: center;
    text-align: center;
   
} 
html{
    font-size: 100%;
    height: 100%;

}
html{
    background: #eee;
}
.box{
    width: 25vw;
    height: 60vh;
    border-radius: 0.5rem;
    box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
    background: #a5bbdd;
    overflow: hidden;
    position: relative;
    transform: translate3d(0, 0, 0);
    min-width: 20rem;
    min-height: 25rem;
    text-align: center;
    margin-left: 500px;
    margin-top: 50px;
}
.wave{
    opacity: 0.3;
    position: absolute;
    top: 120%;
    left: 50%;
    background: white;
    width: 50rem;
    height: 50rem;
    margin-left: -25rem;
    margin-top: -20rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    animation: drift 3000ms infinite linear;
    z-index: 1;
}
.wave-three{
    animation: drift 5000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}
.wave-two{
    animation: drift 7000ms infinite linear;
    z-index: 0.1;
    opacity: 3 !important;
}
.inputField{
    width: 10rem;
    height: 2rem;
    border-radius: 1rem;
    margin-top:50px;
}
